
import {showToast,removeKey} from '@/common/js/utils.js';
let networkHandle = {
	// 
	statusOk (resData) {
		// -99 -240 -205 -121
		if(resData.codeId===-99) {
			removeKey("token")
		}
		if(resData.codeId===-99||resData.codeId===-240) {
			this.feedback(resData)
			return
		}
		if(resData.codeId===-205){
			setTimeout(()=>{
				uni.navigateTo({
					url: '/packageuser/login/register'
				});	
			},1000)
			return
		}
		setTimeout(()=>{
			showToast(resData.codeDes)  
		},200)
	},
	feedback (resData) {
		showToast(resData.codeDes)
		setTimeout(()=>{
			uni.navigateTo({
				url: '/packageuser/login/index'
			});	
		},1000)
	},
	// 
	statusFail (err) {			
		let  str = '';
		if(err && err.response){
			switch (err.response.status) {
			  case 0:
				str = `: ${err.response.config.url}`
				break
			  case 400:
				str = `:${err.response.data.message}`
				break
			  case 401:
				str = ''
				break
			  case 403:
				str = ''
				break
			  case 404:
				str = `: ${err.response.config.url}`
				break
			  case 405:
				str = `:${err.response.data.message}`
				break
			  case 408:
				str = ''
				break
			  case 500:
				str = ''
				break
			  case 501:
				str = ''
				break
			  case 502:
				str = ''
				break
			  case 503:
				str = ''
				break
			  case 504:
				str = ''
				break
			  case 505:
				str = 'HTTP'
				break;
			  default:
				str = `:${err.response.status} `
				break
			}
			if (str) {
			  showToast(str)
			}
		}else if(err.errMsg) {	
		    if (err.errMsg.indexOf("timeout")>-1) {
				showToast("")
			}else if(err.errMsg.indexOf("request:fail")>-1){
				showToast("")
			}else {
				showToast(err.errMsg)
			}
			
		}else if (err.message) {			
			let str = err.message + ""
			if(str.indexOf("statusCode")>-1){
				showToast("")
			}else{
				showToast(err.message)
			}			
		}		
	}	
}

export default networkHandle